.statistics {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding-top: 25px;
  cursor: pointer;
  background-color: #fff;
  box-shadow: var(--boxShadow);
  transition: transform 250ms var(--timingFn);
}

.statistics:hover,
.statistics:focus {
  transform: scale(1.1);
}

.title {
  font-weight: 500;
  text-transform: uppercase;
  font-size: 30px;
  text-align: center;
  letter-spacing: 0.03em;
}
